@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One');

.listContainer {
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;

  .listItem {
    all: initial;
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    color: white;
    position: relative;
    cursor: pointer;
    height: 30px;
    padding: 20px 0;
    border-bottom: 1px dashed #3f4d58;

    img {
      display: none;
    }

    &:hover {
      img {
        display: block;
      }
    }
  }

  .active {
    img {
      display: block;
    }
  }
}
