@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One');

.container {
  color: white;

  .header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 50px 0;

    .title {
      font-family: 'Black Ops One';
      font-size: 50px;
      padding: 20px 0;
    }

    .description {
      font-size: 20px;
    }
  }
  .traitPanel {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 100px 0;

    .traitList {
      width: 300px;
    }
  }

  .categoryContainer {
    width: 90%;
    margin: auto;
    padding: 50px 0;

    .categoryList {
      display: flex;
      justify-content: space-between;
    }
  }

  .footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;

    img {
      width: 100%;
      z-index: 0;
    }

    .title {
      font-family: 'Black Ops One';
      font-size: 50px;
      padding: 20px 0;
    }

    .description {
      font-size: 20px;
    }

    .subscribe {
      width: 50%;
      margin: 100px 0;
      background-color: transparent;
      border-image-slice: 1;
      border-width: 2px;
      border-radius: 40px;
      border-color: #faea74;
      outline: none;
      color: white;
      padding: 20px 40px;
      font-size: 20px;
    }

    .social {
      display: flex;
      margin-bottom: 100px;

      img {
        margin: 0 10px;
        width: 45px;
      }
    }
  }
}
