@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One');

.selectContainer {
  width: 100%;
  height: 50px;
  border-bottom: 5px solid #de9e51;
  text-align: center;
  display: flex;
  flex-direction: column;

  .selected {
    font-family: 'Black Ops One';
    font-size: 30px;
    color: #de9e51;
    text-align: left;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
