.characContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .traitBoard {
    display: flex;
    align-items: center;
    position: relative;

    .leftArrowImg {
      z-index: 3;
      position: absolute;
      left: -100px;
      cursor: pointer;
    }

    .rightArrowImg {
      z-index: 3;
      position: absolute;
      right: -100px;
      cursor: pointer;
    }
  }

  .characBoard {
    z-index: 1;
    position: relative;
    border: 3px solid #de9e51;
    border-radius: 64px;

    .character {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .prev {
    position: absolute;
    z-index: 0;
    left: -100px;
    transform: scale(0.8);
  }

  .next {
    position: absolute;
    z-index: 0;
    right: -100px;
    transform: scale(0.8);
  }
}
