.infoContainer {
  margin-top: 20px;
  background: none;
  padding: 20px;
  border: 3px solid #de9e51;
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
  width: calc(100% - 180px);

  .divider {
    width: 0;
    height: 50px;
    border-right: 1px solid #ae7e01;
  }

  .title {
    color: #ffffff;
    font-size: 25px;
  }

  .traitPercent {
    font-size: 15px;
    color: #de9e51;
  }

  .traitValue {
    font-size: 30px;
    font-weight: 700;
    color: #de9e51;
  }

  .traitText {
    font-size: 15px;
    color: #bbbbbb;
  }
}
