@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One');

.sidebarContainer {
  width: calc(100% - 200px);
  padding: 50px 100px;
  height: 100px;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .logoWrapper {
  }
  .navsWrapper {
    display: flex;
    align-items: center;

    a {
      all: initial;
      display: flex;
      font-size: 20px;
      color: white;
      position: relative;
      cursor: pointer;
      height: 30px;
      margin: 0 10px;

      span {
        font-family: 'Black Ops One';
      }
    }

    .active {
      border-bottom: 10px solid;
      border-image-slice: 1;
      border-width: 5px;
      border-image-source: linear-gradient(#faea74, #be6e01, #faea74);
    }

    .loginWrapper {
      padding-left: 50px;
    }
  }

  .socialWrapper {
    img {
      margin: 0 10px;
    }
  }
}
