@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One');

.colorButton {
  background: linear-gradient(to right, #0060ff, #5070ff);
  border: none;
  border-radius: 5px;
  color: white;
  padding: 10px 50px;
  font-size: 17px;

  &:hover {
    cursor: pointer;
    background: linear-gradient(to right, #0070ff, #7090ff);
  }

  &:active {
    background: linear-gradient(to right, #0060df, #2090ff);
  }

  &:disabled {
    background: none;
    background-color: #aabff6;
  }
}

.outlineButton {
  font-family: 'Black Ops One';
  background: none;
  text-decoration: inherit;
  font-size: 1rem;
  padding: 0.5rem 2rem;
  border: 10px solid;
  border-image-slice: 1;
  border-width: 5px;
  border-image-source: linear-gradient(to bottom, #faea74, #be6e01);
  color: white;

  &:hover {
    cursor: pointer;
    border-image-source: linear-gradient(to right #faea74, #be6e01);
  }

  &:active {
    border-image-source: linear-gradient(to left, #faea74, #be6e01);
  }
}

.decorButton {
  font-family: 'Black Ops One';
  background: none;
  font-size: 25px;
  padding: 10px 0;
  width: 250px;
  border: 10px solid;
  border-image-slice: 1;
  border-width: 5px;
  border-image-source: linear-gradient(to bottom, #faea74, #be6e01);
  color: white;
  position: relative;

  .leftDecor {
    position: absolute;
    top: 20%;
    left: -12px;
  }
  .rightDecor {
    position: absolute;
    top: 20%;
    right: -12px;
  }

  .typeDecor {
    position: absolute;
    left: 0;
    bottom: -15px;
  }

  &:hover {
    cursor: pointer;
    border-image-source: linear-gradient(to bottom, #fa2a74, #fe2e01);
  }

  &:active {
    border-image-source: linear-gradient(to bottom, #fa2a74, #fe2e01);
  }
}

.fire {
  background: linear-gradient(to left, #c42926, #e07c1d, #c42926);
}

.water {
  background: linear-gradient(to left, #0e94be, #4ac9cf, #0e94be);
}

.forest {
  background: linear-gradient(to left, #3b7f13, #63b01e, #3b7f13);
}

.active {
  border-image-source: linear-gradient(to bottom, #fa2a74, #fe2e01);
}
